<template>
  <div class="loading">
    <img width="24" height="24" src="./loading.gif">
    <p class="desc"></p>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'loading',
  props: {
    title: {
      type: String,
      default: '正在载入...'
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import '~common/stylus/variable'
  .loading
    width 100%
    text-align center
    .desc
      line-height 20px
      font-size $font-size-small
      color $color-text-l
</style>



