<template>
  <transition name="confirm-fade">
    <!-- 使得confirm更独立 -->
    <div class="confirm" v-show="showFlag" @click.stop>
      <div class="confirm-wrapper">
        <div class="confirm-content">
          <p class="text">{{text}}</p>
          <div class="operator">
            <div class="operator-btn left" @click="cancel">{{cancelBtnText}}</div>
            <div class="operator-btn" @click="confirm">{{confirmBtnText}}</div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'confirm',
  data () {
    return {
      showFlag: false
    }
  },
  props: {
    text: {
      type: String,
      default: ''
    },
    confirmBtnText: {
      type: String,
      default: '确定'
    },
    cancelBtnText: {
      type: String,
      default: '取消'
    }
  },
  methods: {
    show () {
      this.showFlag = true
    },
    hide () {
      this.showFlag = false
    },
    cancel () {
      this.hide()
      this.$emit('cancel')
    },
    confirm () {
      this.hide()
      this.$emit('confirm')
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import '~common/stylus/variable'
  .confirm
    position fixed
    display flex
    align-items center
    justify-content center
    top 0
    bottom 0
    left 0
    right 0
    z-index 998
    background $color-background-d
    &.confirm-fade-enter-active
      animation confirm-fadein 0.3s
      .confirm-content
        animation confirm-zoom 0.3s
    .confirm-wrapper
      position absolute
      top 50%
      left 50%
      transform translate3d(-50%, -50%, 0)
      z-index 999
      // flex 0 0 270
      // width 270px
      // height 102px
      // background $color-dialog-background
      .confirm-content
        width 270px
        border-radius 13px
        background $color-highlight-background
        // display flex
        // flex-direction column
        // align-items stretch
        // border-radius 5px
        .text
          padding 19px 15px
          line-height 22px
          text-align center
          font-size $font-size-large
          color $color-text-l
          // color $color-text-l
          // font-size $font-size-medium
          // text-align center
          // vertical-align middle
          // border-bottom 1px solid $color-background-d
        .operator
          display flex
          align-items center
          text-align center
          font-size $font-size-large
          .operator-btn
            flex 1
            line-height 22px
            padding 10px 0
            color $color-text-d
            border-top 1px solid $color-background-d
            &.left
              border-right 1px solid $color-background-d
@keyframes confirm-fadein
  0%
    opacity: 0
  100%
    opacity: 1
@keyframes confirm-zoom
  0%
    transform: scale(0)
  50%
    transform: scale(1.1)
  100%
    transform: scale(1)
</style>



